:local(.questions) {

  @-webkit-keyframes pulse {
    0%   {box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);}
    80%  {box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);}
    100% {box-shadow: 0 0 0 15px rgba(125, 159, 137, 0);}
  }
  @keyframes pulse {
    0%   {box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);}
    80%  {box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);}
    100% {box-shadow: 0 0 0 15px rgba(125, 159, 137, 0);}
  }


  position: relative;
  width: 100%;
  height: 100%;
  line-height: 1.4em;
  font-size: 1rem;

  .card {
      .mq-desktop & {
        padding: 30px;
      }
      float: left;
      background: #FFF;
  }

  a {
    text-decoration: none;
    color: #A74405;
  }


  .losButton {
    padding: 10px 30px 10px 30px;
    font-weight: bold;
    background: #7d9f89;
    color: #FFF;
    text-align: center;
    border-radius: 2px;
    text-transform: uppercase;
    cursor: pointer;
    float: left;
    width:100%;
    margin-bottom: 30px;
    margin-top: 10px;
    -webkit-animation: pulse 4s infinite cubic-bezier(0.66, 0, 0, 1);
    animation: pulse 4s infinite cubic-bezier(0.66, 0, 0, 1);

    box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);

    &:hover {
      box-shadow: 0px 3px 4px rgba(0,0,0,0.1);
      background: #577562;
      -webkit-animation: none;animation: none;
    }
  }

  h1 {
    margin-top:0;
    font-size: 1em;
    font-weight: bold;
    line-height: 1.2em;
  }

  p {
    float:left;
  }

  @-webkit-keyframes countdown {
    0%   {
      background-position:right bottom;
    }
    100% {
      background-position:left bottom;
    }
  }


  button {
    margin-top: 20px;
    width: 100%;
    border: none;
    color: #FFF;
    padding: 10px;
    border-radius: 2px;
    font-weight: bold;
    float: left;
    background: linear-gradient(to right, #7D9F89 50%, #395844 50%);
    background-size: 200% 100%;
    -webkit-animation: countdown 10s;
            animation: countdown 10s;
  }

  .answer-tip {
    background: rgba(255, 170, 0, 0.22) !important;
  }

}



